<template>
<div class="popup filters">
    <div class="sub_popup">
        <a class="cerrar" @click="close_popup">x</a>
        <div class="filter show_type">
            <h3 class="titulo">Tipo de show:</h3>
            <ul class="show_type_container">
                <li class="filtro" v-for="(type, subindex) in show_types" :key="subindex" @click="filtering(type)">
                    <p class="parrafo" :class="{'active': filters.type[type]}">{{type}}</p>
                </li>
            </ul>
        </div>
        <div class="filter show_price">
            <h3 class="titulo">Precio mximo:</h3>
            <ul class="show_price_container">
                
            </ul>
        </div>
    </div>
</div>
</template>

<script>
import { mapState } from 'vuex';

export default {
    name: 'AgendaFilters',
    props:[],
    data() {
        return {
            show_types: [],
            filters: {
                type: {},
                price: null
            }
        }
    },
    computed: {
        ...mapState([
            "shows"
        ])
    },
    created() {
    },
    mounted() {
        this.show_types = [];
        this.shows.forEach(show => {
            let type = show.show_type;
            console.log(type);
            if (type && (!this.show_types.includes(type))) {
                this.show_types.push(type);
            }
        });
        this.show_types.forEach(el => {
            this.filters.type[el] = true;
        });
    },
    methods: {
        filtering(filter) {
            this.filters.type[filter] = !this.filters.type[filter];
            this.$emit('filtering', this.filters);
        },
        close_popup() {
            this.$emit('filters_popup', false);
        }
    }
}
</script>
<style scoped lang="scss"> 
    .filters {
        .sub_popup {
            position: relative;
            background-color: #333;
            color: white;
            .cerrar {
                position: absolute;
                top: .5em;
                right: .5em;
                padding: .5em;
            }
            .show_type_container {
                display: flex;
                flex-wrap: wrap;
                .filtro {
                    border: 1px solid white;
                    border-radius: 2em;
                    padding: .2em .5em;
                    background-color: transparent;
                    color: white;
                    opacity: .5;
                    &.active {
                        opacity: 1;
                        background-color: white;
                        color: #333;
                    }
                }
            }
        }
    }
</style>
